const layout = {
	model: "right",
	headerHeight: 60,
	footerHeight: 40,
	tagsView: {
		height: 35,
		toggle: true
	},
	routerPanel: {
		openWidth: 220,
		closeWidth: 40
	},
	settingShow: true,
	padding: 5,
	conditionPanel: {
		width: 320,
		toggle: true
	}
};

export default layout


